.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@import url("https://fonts.googleapis.com/css2?family=Titan+One&family=Titillium+Web&display=swap");

*{
  font-family: "Titan One", sans-serif;
  font-size: 20px;
}

.App {
  background-image: url(https://media.istockphoto.com/id/1183800366/vector/striped-candy-pink-studio-backdrop.jpg?s=612x612&w=0&k=20&c=5uVHmErYOgxfU6s58nZCoc0YJqvy91a4DKK-0NIcuxU=);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: white;
}

input{
  margin-top:5px;
  margin-bottom: 5px;
}

h1{
  font-size: 60px;
  color: red;
  text-shadow: 1px 1px 2px black, 0 0 25px black, 0 0 5px black;

}

h2{
  margin-top: 0px;
  font-size: 30px;
}

ul{
  margin-top: 0px;
  font-size: 20px;
}

button {
  width:100px;
  height: 50px;
  font-size: 20px;
  margin: 5px;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: rgb(209, 63, 88);
  color: white;
}

#start {
  width: 200px;
  margin-bottom: 10px;
}

button:disabled {
  background-color: #ccc;
}

#win{
  margin-top: 10px;
  font-size:25px;
  color: rgb(255, 3, 112);
  text-shadow: 1px 1px 2px black, 0 0 25px black, 0 0 5px black;
}
